
<template>
<div>
  <el-table
      ref="singleTable"
      :data="tableData"
      highlight-current-row
      @current-change="handleCurrentChange"
      style="width: 100%">
    <el-table-column
        type="index"
        width="50">
    </el-table-column>
    <el-table-column
        property="date"
        label="日期"
        width="120">
    </el-table-column>
    <el-table-column
        property="name"
        label="姓名"
        width="120">
    </el-table-column>
    <el-table-column
        property="address"
        label="地址">
    </el-table-column>
  </el-table>
  <div style="margin-top: 20px">
    <el-button @click="setCurrent(tableData[1])">选中第二行</el-button>
    <el-button @click="setCurrent()">取消选择</el-button>
  </div>
</div>
</template>

<script>
export default {
  name: "RadioTable",
  data() {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-04',
        name: '小虎',
        address: '湖北省武汉市 九峰森林 动物园'
      }, {
        date: '2016-05-01',
        name: '小虎',
        address: '湖北省武汉市 东湖飞鸟世界 动物园'
      }, {
        date: '2016-05-03',
        name: '小虎',
        address: '湖北省武汉市 冰雪世界 动物园'
      }],
      currentRow: null
    }
  },

  methods: {
    setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
    },
    handleCurrentChange(val) {
      this.currentRow = val;
    }
  }
}
</script>

